import React, { Component } from 'react';
import './App.css';
import 'simplebar';
import {Link} from 'react-router-dom'

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      about:true,
      solutions:false,
      services:false,
      products:false,
      contact:false
    }
  }
  render() {
    const {about,solutions,services,products,contact} = this.state;
    return (
      <div className="container">
        <div className="row">
          <div className="col-md-2 col-sm-12 bu animated fadeInLeft">
            <Link className="home-btn b" to="/home"></Link>
            <button className="about-btn b" onClick={() => {this.setState({about:true,solutions:false,services:false,products:false,contact:false})}}></button>
            <button className="solutions-btn b" onClick={() => {this.setState({about:false,solutions:true,services:false,products:false,contact:false})}}></button>
          </div>
          <div className="col-md-8 col-sm-12">
            {about &&
            <div className="thumbnail animated fadeInUp" data-simplebar>
              <h3 className="about-h">About Us</h3>
              <p className="about about-p">
              Grand Mentor is a global company that provides sales partnering and program
               management services to the automotive industry suppliers,
                promoting sale processes that keep client ahead of their competitors,
                 helping them retain and grow market share.Our sales solutions reduce cost
                 ,build customer loyalty and accelerate time to market.
                We are sales professionals at pioneering new business and products to the automotive industry.
              </p>
            </div>
            }
            {solutions &&
            <div className="thumbnail" data-simplebar>
              <h3 className="about-h animated fadeInUp">Our Solutions</h3>
              <h4 className="solutions-h animated fadeInUp">Conserve Capital :</h4>
              <p className="about-p animated fadeInUp">Hiring Sales People can be one of the most costly experiences for any business.</p>
              <p className="about-p animated fadeInUp">Recruitment costs, which can be up to 30<span style={{fontFamily:'lato',fontWeight:'lighter'}}>%</span> of the annual Salary package, take a large slice of the cash plus other up-front costs including equipment ,company, car and office space.</p>
              <h4 className="solutions-h animated fadeInUp">Lower your sales overhead :</h4>
              <p className="about-p animated fadeInUp">If you are a business owner, manager or startup entrepreneur looking to grow your business without high costs, risk and hassle of hiring your own sales force, partnering with us can be the answer you are looking for.
                Rather than committing capital and time to hire direct sales people , businesses can access sales resources through our team of sales professionals.
              </p>
              <h4 className="solutions-h animated fadeInUp">Reduced Risk :</h4>
              <p className="about-p animated fadeInUp">In addition to saving huge up-front recruitment fees, many businesses choose to engage our sales team to reduce their risk. Getting a sales hire wrong can be a costly mistake.
                Its not just the wasted recruitment fees and employment costs; failure in hiring the right sales person can lose customers and destroy reputations.
              </p>
              <p className="about-p animated fadeInUp">By working with our team, you can engage with an experienced sales team on a flexible basis. This could be a small project to see how things go before you scale up the operation, with the ability to terminate the relationship (without any employer obligations) at short notice.
              For a business to have the ability to reduce risk by using an outsourced sales team is a big plus.
              </p>
              <h4 className="solutions-h animated fadeInUp">Best Solutions :</h4>
              <p className="about-p animated fadeInUp">We offer immediate automotive satellite sales office located in the heart of automotive industry Detroit, Michigan
              </p>
            </div>
            }
            {services &&
            <div className="thumbnail" data-simplebar>
              <h3 className="about-h animated fadeInUp">services</h3>
              <p className="about-p animated fadeInUp"><strong>Why hire a professional when you can hire our team of professionals for less?</strong></p>
              <p className="about-p animated fadeInUp">Grand Mentor  services are a direct extension of client’s sales efforts offering market knowledge and sales force accountability. An immediate solution that offers a satellite office with experienced automotive sales personnel that become an extension of your marketing efforts.
              </p>
              <p className="about-p animated fadeInUp">Grand Mentor specializes in sales and marketing solutions, program management, strategic account planning ,public relations and professional research that provide sales solutions for high tech and industrial manufacturing companies.
              </p>
              <p className="about-p animated fadeInUp">Grand Mentor LLC areas of expertise is in the automotive industry promoting companies that are serious about reducing cost, increasing revenue, improving credibility with prospects, building industry relationships with automotive companies and suppliers that drive sales and expand awareness in the market place.
              </p>
              <p className="about-p animated fadeInUp">Grand Mentor provides services with companies interested in pioneering and expanding business with automotive manufacturers and automotive parts suppliers
              </p>
              <p className="about-p animated fadeInUp"><strong>Our services Include :</strong> sales and marketing , new business development program management, supplier approval process, new product launch, liaison engineering support, market research , PPAP,APQP and trade shows support.
              </p>
            </div>
            }
            {products &&
            <div className="thumbnail" data-simplebar>
              <h3 className="about-h animated fadeInUp">products</h3>
              <p className="about-p animated fadeInUp">Grand Mentor offers a successful track record of accomplishments in automotive components sales and new business development to major Tier 1 and 2 companies. We possess extensive knowledge of Automotive design , engineering and purchasing , manufacturing and supplier quality procedures. we also bring history of relationship with clients in various divisions of these companies.
              </p>
            </div>
            }
            {contact &&
            <div className="thumbnail" data-simplebar data-simplebar-auto-hide="true">
              <div className="form">
                <h3 className="about-h animated fadeInUp">contact us</h3>
                <p className="about-p reach animated fadeInUp"><strong>Reach out to us for further queries.</strong></p>
                <form className="animated fadeInUp">
                  <div className="form-group">
                    <input className="form-control" type="text" placeholder="Name"/>
                  </div>
                  <div className="form-group">
                    <input className="form-control" type="email" placeholder="Email"/>
                  </div>
                  <div className="form-group">
                    <input className="form-control" type="text" placeholder="phone"/>
                  </div>
                  <div className="form-group">
                    <textarea className="form-control" type="text" placeholder="Send us your message" style={{resize:'none'}}/>
                  </div>
                  <input type="submit" className="send" value="SEND"/>
                </form>
              </div>
              <div className="below-form animated fadeInUp" style={{textAlign:'center'}}>
                <p className="about-p">Grand Mentor LLC</p>
                <p className="about-p">Northville Michigan -48168</p>
                <p className="about-p">Cell : <a href="tel:+248 470 8086" style={{textDecoration:'none',color:'#fff'}}>248 470 8086</a></p>
                <p className="about-p">Email : <a href="mailto:0916vks@gmail.com">0916vks@gmail.com</a></p>
              </div>
            </div>
            }
          </div>
          <div className="col-md-2 col-sm-12 bu animated fadeInRight">
            <button className="services-btn b" onClick={() => {this.setState({about:false,solutions:false,services:true,products:false,contact:false})}}></button>
            <button className="products-btn b" onClick={() => {this.setState({about:false,solutions:false,services:false,products:true,contact:false})}}></button>
            <button className="contact-btn b" onClick={() => {this.setState({about:false,solutions:false,services:false,products:false,contact:true})}}></button>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



